'use client'

import Image from 'next/image'
import Link from 'next/link'
import { useState } from 'react'

interface Department {
name: string
icon: string
doctors: number
link: string
}

interface StatItem {
value: string
label: string
icon: string
}

export default function HomePage() {
const [departments] = useState<Department[]>([
{ name: 'Nagły wypadek', icon: '🚑', doctors: 12, link: '/emergency' },
{ name: 'Kardiolog', icon: '❤️', doctors: 8, link: '/cardiology' },
{ name: 'Pediatra', icon: '👶', doctors: 6, link: '/pediatrics' },
{ name: 'Neurolog', icon: '🧠', doctors: 5, link: '/neurology' },
])

const [stats] = useState<StatItem[]>([
{ value: '24/7', label: 'Usługi ratunkowe', icon: '⏰' },
{ value: '150+', label: 'Lekarze specjaliści', icon: '👨‍⚕️' },
{ value: '500+', label: 'Zadowoleni pacjenci', icon: '😊' },
{ value: '10+', label: 'Oddziały medyczne', icon: '🏥' },
])

return (
<div className="min-h-screen flex flex-col">
{/* Navigation */}
<nav className="bg-white shadow-sm py-4">
<div className="container mx-auto px-4 flex justify-between items-center">
<Link href="/" className="flex items-center space-x-2">
<Image
           src="data:image/jpeg;base64,"
           alt="Hospital logo - modern abstract medical cross symbol"
           width={40}
           height={40}
           className="rounded-full"
         />
<span className="text-xl font-bold text-blue-600">Centralny szpital spierdolenia</span>
</Link>
<div className="hidden md:flex space-x-6">
<Link href="/onas" className="text-gray-600 hover:text-blue-600 transition-colors">O nas</Link>
<Link href="/uslugi" className="text-gray-600 hover:text-blue-600 transition-colors">Usługi</Link>
<Link href="/doctors" className="text-gray-600 hover:text-blue-600 transition-colors">Kadra medyczna</Link>
<Link href="/contact" className="text-gray-600 hover:text-blue-600 transition-colors">Kontakt</Link>
</div>
<Link href="/login" className="btn-secondary hover:bg-blue-600 p-2 rounded-lg text-blue-600 hover:text-white hover:scale-105 transition-transform duration-600">
Patient Portal
</Link>
</div>
</nav>

  {/* Hero Section */}

  <section className="relative bg-gradient-to-r from-blue-600 to-indigo-700 text-white py-20">

    <div className="container mx-auto px-4 flex flex-col md:flex-row items-center">

      <div className="md:w-1/2 mb-10 md:mb-0">

        <h1 className="text-4xl md:text-5xl font-bold mb-6 leading-tight">

         Zaawansowana opieka medyczna <br />z troską i współczuciem

        </h1>

        <p className="text-lg md:text-xl mb-8 max-w-lg">

          Nasz szpital zapewnia najwyższej jakości usługi opieki zdrowotnej, wykorzystując najnowocześniejsze technologie oraz spersonalizowane podejście.

        </p>

        <div className="flex flex-col sm:flex-row gap-4">

          <Link href="/appointment" className="btn-primary">

            Umów wizytę

          </Link>

          <Link 

            href="/departments" 

            className="btn-secondary bg-transparent border-white text-white hover:bg-white hover:text-blue-600"

          >

            Nasze oddziały

          </Link>

        </div>

      </div>

      <div className="md:w-1/2 flex justify-center">

      <Image

        src="data:image/jpeg;base64,"

        alt="Hospital logo - modern abstract medical cross symbol"

        width={1000}

        height={1000}
        className='p-2'



      />


      </div>

    </div>

  </section>


  {/* Stats Section */}

  <section className="py-16 bg-white">

    <div className="container mx-auto px-4">

      <h2 className="text-3xl font-bold text-center mb-12">Dlaczego warto wybrać nasz szpital</h2>

      <div className="grid grid-cols-2 md:grid-cols-4 gap-6">

        {stats.map((stat, index) => (

          <div key={index} className="text-center p-6 rounded-lg bg-gray-50 hover:bg-blue-50 transition-colors hover:scale-105 transition-transform duration-600" >

            <div className="text-3xl mb-3">{stat.icon}</div>

            <div className="text-3xl font-bold text-blue-600 mb-2">{stat.value}</div>

            <div className="text-gray-600">{stat.label}</div>

          </div>

        ))}

      </div>

    </div>

  </section>


  {/* Departments Section */}

  <section className="py-16 bg-gray-50">

    <div className="container mx-auto px-4">

      <h2 className="text-3xl font-bold text-center mb-12">Oddziały medyczne</h2>

      

      <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6 ">

        {departments.map((dept, index) => (

          <Link 

            href={dept.link} 

            key={index} 

            className="hospital-card hover:-translate-y-1 hover:scale-105 transition-transform duration-400"

          >

            <div className="p-6">

              <div className="text-4xl mb-4">{dept.icon}</div>

              <h3 className="text-xl font-semibold mb-2">{dept.name}</h3>

              <p className="text-gray-600 mb-4">

                {dept.doctors}  {dept.doctors === 1 ? 'doktor' : 'doktorów'} dostępnych

              </p>

              <div className="text-blue-600 hover:underline font-medium flex items-center">

               Zobacz szczegóły

                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 ml-1" viewBox="0 0 20 20" fill="currentColor">

                  <path fillRule="evenodd" d="M12.293 5.293a1 1 0 011.414 0l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-2.293-2.293a1 1 0 010-1.414z" clipRule="evenodd" />

                </svg>

              </div>

            </div>

          </Link>

        ))}

      </div>

    </div>

  </section>


  {/* Emergency Section */}

  <section className="py-16 bg-white">

    <div className="container mx-auto px-4 flex flex-col md:flex-row items-center">

      <div className="md:w-1/2 mb-8 md:mb-0">

           <Image

              src="data:image/jpeg;base64,"

              alt="Hospital logo - modern abstract medical cross symbol"

              width={600}

              height={600}
              className='ml-2'


            />


      </div>

      <div className="md:w-1/2 md:pl-12">

        <h2 className="text-3xl font-bold mb-6">Całodobowe usługi ratunkowe (24/7)</h2>

        <p className="text-lg mb-6 text-gray-700">

          Nasz oddział ratunkowy jest obsługiwany przez certyfikowanych lekarzy medycyny ratunkowej, gotowych do natychmiastowego udzielenia pomocy w przypadku poważnych chorób i urazów.

        </p>

        <div className="bg-red-50 border-l-4 border-red-500 p-4 mb-6">

          <p className="font-semibold text-red-800">W przypadku nagłego wypadku:</p>

          <p className="text-2xl font-bold text-red-600">Zadzwoń +48 123 456 789</p>

        </div>

        <div className="flex flex-col sm:flex-row gap-4">

          <Link href="/emergency" className="btn-primary bg-red-600 hover:bg-red-700">

            Informacje ratunkowe


          </Link>

          <Link href="/emergency-contacts" className="btn-secondary border-red-600 text-red-600 hover:bg-red-50">

            Kontakty alarmowe

          </Link>

        </div>

      </div>

    </div>

  </section>


  {/* Footer */}

  <footer className="bg-gray-800 text-white py-12 mt-auto">

    <div className="container mx-auto px-4">

      <div className="grid grid-cols-1 md:grid-cols-4 gap-8">

        <div>

          <h3 className="text-xl font-bold mb-4">Centralny szpital spierdolenia</h3>

          <p className="text-gray-300">Zapewniamy wyjątkową opiekę zdrowotną od 1985 roku.</p>

        </div>

        <div>

          <h4 className="text-lg font-semibold mb-4">Szybkie łącza</h4>

          <ul className="space-y-2">

            <li><Link href="/onas" className="text-gray-300 hover:text-white transition-colors">O nas</Link></li>

            <li><Link href="/doctors" className="text-gray-300 hover:text-white transition-colors">Nasi doktorzy</Link></li>

            <li><Link href="/services" className="text-gray-300 hover:text-white transition-colors">Usługi</Link></li>

            <li><Link href="/appointment" className="text-gray-300 hover:text-white transition-colors">Umów wizytę</Link></li>

          </ul>

        </div>

        <div>

          <h4 className="text-lg font-semibold mb-4">Kontakt</h4>

          <address className="text-gray-300 not-italic">

            <p>123 Medical Avenue</p>

            <p>Warsaw, Poland</p>

            <p>Phone: +48 123 456 789</p>

            <p>Email: contact@hospital.example</p>

          </address>

        </div>

        <div>

          <h4 className="text-lg font-semibold mb-4">Ratunek</h4>

          <p className="text-gray-300 mb-2">Całodobowy oddział ratunkowy (24/7)</p>

          <p className="text-xl font-bold text-red-400">+48 123 456 789</p>


        </div>

      </div>

      <div className="border-t border-gray-700 mt-8 pt-8 text-center text-gray-400">

       <p>© {new Date().getFullYear()} Miejski Szpital Ogólny. Wszelkie prawa zastrzeżone.</p>


      </div>

    </div>

  </footer>

</div>

)
}

